:root {
  --white: #eee;
  --theme-color: #397eed;
  --font: 'Oswald', sans-serif;
}

body {
  font-family: 'Oswald', sans-serif;
  font-family: var(--font);
}

h2 {
  background-color: #397eed;
  background-color: var(--theme-color);
  color: #eee;
  color: var(--white);
  font-size: 5rem;
  padding: 50px;
  margin: 0;
  text-align: center;
}


.navbar {
    display: flex;
    flex-direction: column;
}

.navbar-hamburger {
    display: flex;
    justify-content: flex-end;
    order: -1;
}

.hidden {
    display: none;
}

#Header {
    background-color: var(--theme-color);
    color: var(--white);
    padding: 20px;
}

.logo {
    border: 5px solid var(--white);
    border-radius: 100%;
    font-size: 3rem;
    font-weight: bold;
    padding: 10px;
    width: 60px;
}

.hero {
    align-items: center;
    display: flex;
    height: 650px;
    justify-content: center;
}

.hero-content {
    text-align: center;
}

.hero-content h1 {
    font-size: 5rem;
}

.hero-content p {
    font-size: 1.5rem;
}
.hero {
    align-items: center;
    background-color: var(--theme-color);
    color: var(--white);
    display: flex;
    height: 650px;
    justify-content: center;
}

.hero-content {
    text-align: center;
}

.hero-content h1 {
    font-size: 5rem;
}

.hero-content p {
    font-size: 1.5rem;
}

.about-me-content {
    display: flex;
    margin: 20px auto;
    width: 80%;
}

.about-me-content * {
    margin: 10px;
}

.about-me-image {
    width: 25%;
}

.about-me-content {
    font-size: 1.5rem;
}

@media only screen and (max-width: 800px) {
    .about-me-content {
        display: block;
    }

    .about-me-image {
        width: 100%;
    }
}
.project img {
    width: 100%;
}


.form-status {
    color: #189e18;
}



#Footer {
    background-color: var(--theme-color);
    color: var(--white);
    padding: 20px;
}
